import React from "react";
import classes from "./SelectCareerDialog.module.css";
import Assets, { Career } from "Common/assets";
import { useDispatch, useSelector } from "react-redux";
import {
  closeMainDialog,
  openSelectCardDialog,
  filter,
} from "ActionCreators/actionCreators";
import { Career as CareerNames } from "GameLogic/Cards/Types";
import { selectFilteredHandCards } from "Selectors/Selectors";

export default function SelectCareerDialog() {
  const careerFilter = (career) =>
    selectFilteredHandCards((item) => item.career === career);
  const warriorExists = useSelector(careerFilter(CareerNames.warrior));
  const smithExists = useSelector(careerFilter(CareerNames.smith));
  const farmerExists = useSelector(careerFilter(CareerNames.farmer));
  const minerExists = useSelector(careerFilter(CareerNames.miner));

  const dispatch = useDispatch();
  const cancelDialog = () => {
    dispatch(closeMainDialog());
  };
  const space = 60; //TODO Ð¸Ð·Ð±Ð°Ð²Ð¸ÑÑÑÑ Ð¾Ñ ÑÐ°ÑÐ´ÐºÐ¾Ð´Ð° - Ð½ÐµÐºÐ¾ÑÑÐµÐºÑÐ½Ð¾ Ð¾ÑÐ¾Ð±ÑÐ°Ð¶Ð°ÐµÑÑÑ Ð´Ð»Ñ iPAD
  const width = space * 0.449;
  const height = space * 0.65;

  const optionStyle = (asset) => ({
    height: height + "vh",
    width: width + "vh",
    backgroundImage: `url(${asset})`,
    backgroundRepeat: "no-repeat",
  });

  const runeStyle = {
    width: width * 0.8 + "vh",
    height: width * 0.8 + "vh",
    backgroundRepeat: "no-repeat",
    marginTop: width * 0.1 + "vh",
  };

  const openSelectionDialog = (career) => () => {
    dispatch(filter(career));
    dispatch(openSelectCardDialog(null, [], null));
  };

  const disabledStyle = {
    opacity: 0.6,
  };

  return (
    <div className={classes.SelectDialog}>
      <div className={classes.Header}>Career selection</div>
      <div className={classes.Wrapper}>
        <div
          className={classes.CardOptionWrapper}
          style={!warriorExists ? disabledStyle : null}
        >
          <div
            style={optionStyle(Career.war_option)}
            onClick={warriorExists && openSelectionDialog(CareerNames.warrior)}
          ></div>
          <img
            style={runeStyle}
            src={Assets.runes["monster"]}
            alt={"monster"}
            onClick={warriorExists && openSelectionDialog(CareerNames.warrior)}
          />
        </div>
        <div
          className={classes.CardOptionWrapper}
          style={!smithExists ? disabledStyle : null}
        >
          <div
            style={optionStyle(Career.smith_option)}
            onClick={openSelectionDialog(CareerNames.smith)}
          ></div>
          <img
            style={runeStyle}
            src={Assets.runes["fortuna"]}
            alt={"fortuna"}
            onClick={openSelectionDialog(CareerNames.smith)}
          />
        </div>
        <div
          className={classes.CardOptionWrapper}
          style={!farmerExists ? disabledStyle : null}
        >
          <div
            style={optionStyle(Career.farmer_option)}
            onClick={farmerExists && openSelectionDialog(CareerNames.farmer)}
          ></div>
          <img
            style={runeStyle}
            src={Assets.runes["nature"]}
            alt={"nature"}
            onClick={farmerExists && openSelectionDialog(CareerNames.farmer)}
          />
        </div>
        <div
          className={classes.CardOptionWrapper}
          style={!minerExists ? disabledStyle : null}
        >
          <div
            style={optionStyle(Career.miner_option)}
            onClick={minerExists && openSelectionDialog(CareerNames.miner)}
          ></div>
          <img
            style={runeStyle}
            src={Assets.runes["research"]}
            alt={"research"}
            onClick={minerExists && openSelectionDialog(CareerNames.miner)}
          />
        </div>
      </div>
      <div className={classes.ButtonsWrapper}>
        <div className={classes.Cancel} onClick={cancelDialog}>
          Cancel
        </div>
      </div>
    </div>
  );
}
