import React from "react";
import classes from "./AvatarSelection.module.css";
import { connect, useSelector, useDispatch } from "react-redux";

import Avatar from "./Avatar";
import Buttons from "./Button";
import {
  setAvaterColorId,
  setAvatarImageID,
  changeReady,
} from "ActionCreators/actionCreators";

const AvatarSelection = () => {
  const dispatch = useDispatch();
  const onMessage = (device_id, action) => {
    dispatch(action);
  };
  const setAvatarColor = (colorID) => dispatch(setAvaterColorId(colorID));
  const setAvatarImage = (imageID) => dispatch(setAvatarImageID(imageID));
  const changeState = () => dispatch(changeReady());

  const airconsole = useSelector((state) => state.services.airconsole);
  let nickName = useSelector((state) => state.game.nickName);
  const colorID = useSelector((state) => state.game.colorID);
  const imageID = useSelector((state) => state.game.imageID);
  const isReady = useSelector((state) => state.services.isReady);

  airconsole.onMessage = onMessage;

  // Ð£ÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ ÑÐ²ÐµÑ Ð°Ð²Ð°ÑÐ°ÑÐ°
  const setColor = (id) => {
    if (isReady) return;
    setAvatarColor(id);
    airconsole.setCustomDeviceState({
      colorID: id,
      imageID,
      nickName,
      isReady,
    });
  };

  // Ð£ÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð¸Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ
  const setAvatar = (id) => {
    if (isReady) return;
    setAvatarImage(id);
    airconsole.setCustomDeviceState({
      colorID,
      imageID: id,
      nickName,
      isReady,
    });
  };

  const changeStateAndSend = () => {
    changeState();
    airconsole.message({ isReady: !isReady });
  };

  return (
    <div className={classes.box}>
      <Buttons
        text={"<"}
        setColorId={setColor}
        colorID={colorID}
        dir={-1}
        setAvatarId={setAvatar}
        imageID={imageID}
      />
      <Avatar
        imageID={imageID}
        isReady={isReady}
        colorID={colorID}
        nickName={nickName}
        changeState={changeStateAndSend}
      />
      <Buttons
        text={">"}
        setColorId={setColor}
        colorID={colorID}
        dir={+1}
        setAvatarId={setAvatar}
        imageID={imageID}
      />
    </div>
  );
};

export default AvatarSelection;
