import { lang } from "Common/constants";
import { openCreateDialog } from "ActionCreators/actionCreators";

export default class CardOperations {
  constructor(card) {
    this.card = card;
  }

  getOperations() {
    return [
      {
        title: lang.create,
        action: openCreateDialog(this.card, null),
      },
    ];
  }
}
