import React, { useRef, useState } from "react";
import classes from "./CardContextMenu.module.css";
import { useDispatch } from "react-redux";

export default function CardContextMenu({ options = [], width, height }) {
  return (
    <div className={classes.MenuWrapper}>
      {options.map((option, index) => (
        <CardMenuItem
          key={index}
          index={index}
          option={option}
          width={width}
          height={height}
        />
      ))}
    </div>
  );
}

function CardMenuItem({ option, width, height, index }) {
  const dispatch = useDispatch();
  const itemWidth = width * 1.1;
  const itemHeight = height * 0.2;
  const elementRef = useRef();
  const [dir, setDir] = useState("left");
  const itemStyle = {
    width: `${itemWidth}vh`,
    height: `${itemHeight}vh`,
    fontSize: `${itemHeight * 0.7}vh`,
    top: index * itemHeight + (index ? 3 : 0) + "vh",
    [dir]: `${itemWidth * 0.5}vh`,
  };

  const onClickHandler = () => {
    if (option.action) {
      dispatch(option.action);
    }
  };

  const calculateDir = () => {
    if (!elementRef.current) {
      setTimeout(() => {
        calculateDir();
      }, 300);
      return;
    }

    const clientRect = elementRef.current.getBoundingClientRect();

    if (window.innerWidth < clientRect.left + clientRect.width) {
      setDir("right");
    }
  };

  const setRef = (el) => {
    elementRef.current = el;
    calculateDir();
  };

  return (
    <div
      ref={setRef}
      key={index}
      style={itemStyle}
      className={
        classes.MenuItem +
        " " +
        (dir === "left" ? classes.EmergenceLeft : classes.EmergenceRight)
      }
      onClick={onClickHandler}
    >
      {option.title}
    </div>
  );
}
