import { createStore, combineReducers, applyMiddleware } from "redux";
import { privat, services, AvatarSelection, game } from "Reducers/reducers";
import initialState from "./initialState";
import createSagaMiddleware from "redux-saga";
import { sagaWatcher } from "Sagas/sagas";

const saga = createSagaMiddleware();

// Ð¤Ð°Ð±ÑÐ¸ÐºÐ° ÑÑÐ°Ð½Ð¸Ð»Ð¸ÑÐ°
export default (airconsole) => {
  initialState.services.airconsole = airconsole;

  // Ð¥ÑÐ°Ð½Ð¸Ð»Ð¸ÑÐµ
  const store = createStore(
    combineReducers({ privat, services, AvatarSelection, game }),
    initialState,
    applyMiddleware(saga)
  );
  saga.run(sagaWatcher);
  airconsole.store = store;

  // ÐÐ¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ ÑÑÐ°Ð½Ð¸Ð»Ð¸ÑÐµ
  return store;
};
