import React from "react";
import classes from "./Resources.module.css";
import Assets from "Common/assets";
import { useSelector } from "react-redux";
import { selectColorID, selectResources } from "Selectors/Selectors";
import { colors } from "Common/constants";
import Inventory from "./Inventory";

const Resources = () => {
  const colorID = useSelector(selectColorID);
  const resources = useSelector(selectResources);
  const imgStyle = {
    width: "13.5vw",
    height: "15vh",
  };

  const longBoxStyle = {
    display: "flex",
    backgroundColor: colors[colorID],
  };
  return (
    <React.Fragment>
      <div>
        <div className={classes.Resources}>
          <div className={classes["long-box"]} style={longBoxStyle}>
            <div className={classes["res-box"]}>
              <img src={Assets.stone} style={imgStyle} alt={"stone"} />
            </div>
            <div className={classes["res-box"]}>
              <img src={Assets.wood} style={imgStyle} alt={"wood"} />
            </div>
            <div className={classes["res-box"]}>
              <img src={Assets.food} style={imgStyle} alt={"food"} />
            </div>
            <div className={classes["res-box"]}>
              <img src={Assets.gold} style={imgStyle} alt={"gold"} />
            </div>
            <div className={classes["res-box"]}>
              <img src={Assets.diamond} style={imgStyle} alt={"diamond"} />
            </div>
          </div>
        </div>
        <div className={classes.ResoursesCount}>
          <div className={classes["long-count"]}>
            <div className={classes["res-count"]}>{resources.stone}</div>
            <div className={classes["res-count"]}>{resources.wood}</div>
            <div className={classes["res-count"]}>{resources.food}</div>
            <div className={classes["res-count"]}>{resources.gold}</div>
            <div className={classes["res-count"]}>{resources.diamond}</div>
          </div>
        </div>
      </div>
      <div className={classes.Inventory}>
        <Inventory />
      </div>
    </React.Fragment>
  );
};

export default Resources;
