import React from "react";
import classes from "./SelectDialog.module.css";
import Hand from "../../Boards/Cards/Hand";
import { useDispatch, useSelector } from "react-redux";
import { selectMainDialog } from "Selectors/Selectors";
import {
  closeMainDialog,
  filter,
  openCreateDialog,
} from "ActionCreators/actionCreators";

const SelectDialog = () => {
  const dispatch = useDispatch();
  const dialog = useSelector(selectMainDialog);
  const { params } = dialog;
  const clickHandler = (card) => () => {
    const newDialog = params.prevDialog;
    const { firstCard } = newDialog?.params ?? {};
    card.select = false;
    card.creation = true;
    if (newDialog && firstCard) {
      if (params.replaceId === firstCard.id) {
        card.shirt = firstCard.shirt;
        card.shirt_other = firstCard.shirt_other;
        newDialog.params.firstCard = card;
      } else {
        card.shirt = !firstCard.shirt;
        card.shirt_other = !firstCard.shirt_other;
        newDialog.params.secondCard = card;
      }
      dispatch(filter(null));
      dispatch(closeMainDialog());
    } else {
      card.shirt = false;
      card.shirt_other = false;
      dispatch(filter(null));
      dispatch(openCreateDialog(card, null));
    }
  };

  return (
    <div className={classes.SelectDialog}>
      <div className={classes.Header}>Card selection</div>
      <Hand
        select={true}
        excludeIds={params.excludeIds}
        clickHandler={clickHandler}
      />
    </div>
  );
};

export default SelectDialog;
