import HandCard from "./HandCard";
import { Career, Resource, Items, Content } from "GameLogic/Cards/Types";

export function randomInteger(min, max) {
  // ÑÐ»ÑÑÐ°Ð¹Ð½Ð¾Ðµ ÑÐ¸ÑÐ»Ð¾ Ð¾Ñ min Ð´Ð¾ (max+1)
  let rand = min + Math.random() * (max + 1 - min);
  return Math.floor(rand);
}

const generateCareer = () => {
  const CareerKeys = Object.keys(Career);
  return Career[CareerKeys[randomInteger(0, CareerKeys.length - 1)]];
};

const generateContent = (career) => {
  const contentArray = Content[career];
  return {
    value: contentArray[randomInteger(0, contentArray.length - 1)],
    count: randomInteger(1, 3),
  };
};

export default function generateHandCards(count) {
  const generatedCards = [];
  for (let i = 0; i < count; ++i) {
    const career = generateCareer();
    const content = generateContent(career);
    generatedCards.push(new HandCard(career, content, content.value));
  }

  return generatedCards;
}
