import React, { useEffect } from "react";
import classes from "./Layout.module.css";

import ViewManager from "../ViewManager";
import DevelopTools from "Containers/DevelopTools";

// Ð ÐµÐ¶Ð¸Ð¼ ÑÐ°Ð·ÑÐ°Ð±Ð¾ÑÐºÐ¸
const dev = !true;
// Layout Ð¿ÑÐµÐ·ÐµÐ½ÑÐ°ÑÐ¸Ð¾Ð½Ð½ÑÐ¹ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ
const Layout = () => {
  useEffect(() => {
    const kek = function (event) {
      // alert("");
      event.preventDefault();
      // event.stopPropagation();
      return false;
    };
    window.oncontextmenu = kek;

    document.oncontextmenu = kek;
  }, []);

  var requestFullscreen = function (ele) {
    if (ele.requestFullscreen) {
      ele.requestFullscreen();
    } else if (ele.webkitRequestFullscreen) {
      ele.webkitRequestFullscreen();
    } else if (ele.mozRequestFullScreen) {
      ele.mozRequestFullScreen();
    } else if (ele.msRequestFullscreen) {
      ele.msRequestFullscreen();
    } else {
      console.log("Fullscreen API is not supported.");
    }
  };
  let fullScreen = false;
  let lol = () => {
    if (!fullScreen) {
      requestFullscreen(document.documentElement);
    }
    lol = null;
  };
  return (
    <div onClick={lol} className={classes["Layout"]}>
      {dev ? <DevelopTools /> : null}
      <ViewManager />
    </div>
  );
};

export default Layout;
