import React from "react";
import classes from "./Hand.module.css";
import Filter from "../Components/Filter";
import GameCards from "../Components/Cards/Cards";
import { useSelector } from "react-redux";
import { selectHandCards } from "Selectors/Selectors";
import Assets from "Common/assets";

// import { HandCards } from "../Table/Mock";
const Hand = ({ select, excludeIds, clickHandler }) => {
  let HandCards = useSelector(selectHandCards);
  const emptyHands = HandCards.length === 0;
  let HandCardsClone = !emptyHands ? [...HandCards] : [];
  if (select) {
    HandCardsClone = HandCardsClone.map((card) => ({ ...card, select }));
  }

  if (excludeIds) {
    HandCardsClone = HandCardsClone.filter(
      (card) => !excludeIds.find((id) => id === card.id)
    );
  }

  const emptyHangsStyles = {
    width: "55%",
    height: "60%",
    backgroundRepeat: "no-repeat",
    backgroundImage: `url(${Assets.no_cards})`,
  };

  return (
    <div className={classes.Hand}>
      <Filter />
      {emptyHands ? null : (
        <GameCards
          cards={HandCardsClone}
          space={52}
          hand={true}
          select={select}
          clickHandler={clickHandler}
        />
      )}
      {!emptyHands ? null : (
        <div className={classes.EmptyWrapper}>
          <div style={emptyHangsStyles}></div>
        </div>
      )}
    </div>
  );
};

export default Hand;
