import React from "react";
import classes from "./Button.module.css";

// ÐÐ¾Ð½Ð¾Ð¿ÐºÐ¸ ÑÐ¼ÐµÐ½Ñ Ð°Ð²Ð°ÑÐ°ÑÐ°
export default ({ text, setColorId, colorID, dir, setAvatarId, imageID }) => {
  const setColor = () => {
    colorID += dir;
    if (colorID === -1) colorID = 3;
    if (colorID === 4) colorID = 0;
    setColorId(colorID);
  };

  const setAvatar = () => {
    imageID += dir;
    if (imageID === -1) imageID = 3;
    if (imageID === 4) imageID = 0;
    setAvatarId(imageID);
  };
  return (
    <div className={classes.buttonsBlock}>
      <button className={classes.btnBig} onClick={setAvatar}>
        {text}
      </button>
      <button className={classes.btn} onClick={setColor}>
        {text}
      </button>
    </div>
  );
};
