// ÐÑÐµÐ¾Ð±ÑÐ°Ð·ÑÐµÑ ÑÐ»ÑÐ¶ÐµÐ±Ð½ÑÐµ Ð´Ð°Ð½Ð½ÑÐµ
export const services = (state = {}, action) => {
  switch (action.type) {
    // ÐÐ·Ð¼ÐµÐ½Ð¸ÑÑ Ð¿ÑÐµÐ´ÑÑÐ°Ð²Ð»ÐµÐ½Ð¸Ðµ
    case C.CHANGE_VIEW:
      return CHANGE_VIEW(state, action);
    // ÐÐ·Ð¼ÐµÐ½Ð¸ÑÑ ÑÐ¾ÑÑÐ¾ÑÐ½Ð¸Ðµ Ð³Ð¾ÑÐ²Ð¾Ð½Ð¾Ð¾ÑÑÐ¸
    case C.CHANGE_READY:
      return {
        ...state,
        isReady: !state.isReady,
      };
    // ÐÑÐºÑÑÑÑ Ð´Ð¸Ð°Ð»Ð¾Ð³ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ñ ÐºÐ°ÑÑ
    case C.OPEN_CREATION_DIALOG:
      return {
        ...state,
        dialog: action.dialog,
      };
    // ÐÑÐºÑÑÑÑ Ð¿ÑÐµÐ·ÐµÐ½ÑÐ°ÑÐ¸Ñ ÑÐ¾Ð»ÑÐºÐ¾ ÑÑÐ¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð½ÑÑ ÐºÐ°ÑÑ
    case C.OPEN_NEW_HAND_CARDS_DIALOG:
      return {
        ...state,
        dialog: action.dialog,
      };
    // ÐÐ°ÐºÑÑÑÑ Ð³Ð»Ð°Ð²Ð½ÑÐ¹ Ð´Ð¸Ð°Ð»Ð¾Ð³
    case C.CLOSE_MAIN_DIALOG:
      return {
        ...state,
        dialog: state.dialog?.params?.prevDialog,
      };
    // ÐÐµÑÐµÐ²ÐµÑÐ½ÑÑÑ ÐºÐ°ÑÑÑ Ð² Ð´Ð¸Ð°Ð»Ð¾Ð³Ðµ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ñ ÐºÐ°ÑÑ
    case C.SWAP_CARD_CREATION:
      return {
        ...state,
        dialog: swapCardCreationReducer(state.dialog),
      };
    // Ð¡Ð¼ÐµÐ½Ð¸ÑÑ Ð²ÐºÐ»Ð°Ð´ÐºÑ Ð½Ð° ÑÑÑÐ°Ð½Ð¸ÑÐµ ÐºÐ°ÑÑ
    case C.SET_MAIN_CARD_TAB:
      return {
        ...state,
        mainCardTab: action.value,
      };
    default:
      return state;
  }
};

