// ÐÑÐµÐ¾Ð±ÑÐ°Ð·ÑÐµÑ Ð»Ð¸ÑÐ½ÑÐµ Ð´Ð°Ð½Ð½ÑÐµ
export const game = (state = {}, action) => {
  switch (action.type) {
    // Ð¡Ð¼ÐµÐ½Ð¸ÑÑ ÑÐ²ÐµÑ Ð°Ð²Ð°ÑÐ°ÑÐ°
    case C.SET_AVATAR_COLOR:
      return {
        ...state,
        colorID: action.colorID,
      };
    // ÐÐ·Ð¼ÐµÐ½Ð¸ÑÑ Ð¼Ð·Ð¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ Ð°Ð²Ð°ÑÐ°ÑÐ°
    case C.SET_AVATAR_IMAGE:
      return {
        ...state,
        imageID: action.imageID,
      };
    // Ð£ÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð½Ð¸ÐºÐ½ÐµÐ¹Ð¼
    case C.SET_NICKNAME:
      return {
        ...state,
        nickName: action.nickName,
      };

    // ÐÐ¾Ð»Ð½Ð¾ÑÑÑÑ Ð¸Ð·Ð¼ÐµÐ½Ð¸ÑÑ Ð¾Ð±ÑÐµÐºÑ
    case C.SET_GAME_STATE:
      return action.game;

    // ÐÐ½Ð¸ÑÐ¸Ð°Ð»Ð¸Ð·Ð°ÑÐ¸Ñ Ð¸Ð³ÑÑ
    case C.INIT_GAME:
      return {
        ...state,
        handCards: [],
        isMyTurn: false,
      };

    // Ð£ÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð¼Ð¾Ð¹ Ð»Ð¸ ÑÐ¾Ð´
    case C.SET_TURN:
      return {
        ...state,
        isMyTurn: action.isMyTurn,
      };
    // ÐÐµÑÐµÐ²ÐµÑÐ½ÑÑÑ Ð¾Ð¿ÑÐµÐ´ÐµÐ»ÐµÐ½Ð½ÑÑ ÐºÐ°ÑÑÑ
    case C.ROLL_CARD:
      return {
        ...state,
        handCards: rollCardReducer(state.handCards, action.card_id),
      };
    // Ð£ÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð·Ð½Ð°ÑÐµÐ½Ð¸Ðµ ÑÐ¸Ð»ÑÑÑÐ°
    case C.SET_FILTER:
      return setFilter(state, action);
    // Ð¡Ð¾Ð·Ð´Ð°ÑÑ Ð½Ð¾Ð²ÑÑ ÐºÐ°ÑÑÑ Ð¸ ÑÐ´Ð°Ð»Ð¸ÑÑ 2 Ð¸Ð· ÐºÐ¾ÑÐ¾ÑÑÑ ÑÐ¾Ð±ÑÐ°Ð½Ð° Ð½Ð¾Ð²Ð°Ñ
    case C.CREATE_TABLE_CARD:
      return createCardReduced(state, action);
    // Ð¢Ð¾ÑÐµÑÐ½Ð¾ Ð¿ÐµÑÐµÐ²ÐµÑÐ½ÑÑÑ ÐºÐ°ÑÑÑ
    case C.CHANGE_SHIRT:
      return {
        ...state,
        handCards: setShirtReducer(state.handCards, action),
      };

    // ÐÐµÑÐ½ÑÑÑ ÐºÐ°ÑÑÑ Ð² Ð¸ÑÑÐ¾Ð´Ð½Ð¾Ðµ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ
    case C.RESET_CARDS:
      return {
        ...state,
        handCards: resetHand(state.handCards),
      };
    // ÐÐ¾Ð±Ð°Ð²Ð¸ÑÑ Ð´Ð°Ð½Ð½ÑÐµ ÐºÐ°ÑÑÑ Ðº ÐºÐ°ÑÑÐ°Ð¼ Ð½Ð° ÑÑÐºÐµ
    case C.ADD_HAND_CARDS:
      return {
        ...state,
        handCards: [...action.newHandCards, ...state.handCards],
      };
    default:
      return state;
  }
};

