import React from "react";
import classes from "./MainMenu.module.css";
import { NavLink } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { imagesPath, avatarImages, avatarColors } from "Common/constants";
import {
  selectColorID,
  selectImageID,
  selectNickName,
  selectRunes,
  selectIsMyTurn,
} from "Selectors/Selectors";
import Assets from "Common/assets";
import { commitRoundStep } from "ActionCreators/actionCreators";
const MainMenu = (props) => {
  const dispatch = useDispatch();
  const colorID = useSelector(selectColorID);
  const imageID = useSelector(selectImageID);
  const nickName = useSelector(selectNickName);
  const runes = useSelector(selectRunes);
  const isMyTurn = useSelector(selectIsMyTurn);

  let avatar = {
    backgroundImage: "url(" + imagesPath + avatarColors[colorID] + ")",
    position: "relative",
    overflow: "hidden",
  };

  const commitStep = () => {
    if (isMyTurn) {
      console.log("commit");
      dispatch(commitRoundStep());
    }
  };

  return (
    <div className={classes.MainMenu}>
      <div className={classes.tabs}>
        <div className={classes.AvatarBox}>
          <img
            className={classes.avatar__img}
            style={avatar}
            src={imagesPath + avatarImages[imageID]}
            alt="avatar"
          />
        </div>
        <div className={classes.NickName}>{nickName}</div>
        <NavLink
          to="/cards"
          className={classes.Link + " " + classes.tab}
          activeClassName={classes.Link_active}
        >
          Cards
        </NavLink>

        <NavLink
          to="/resources"
          className={classes.Link + " " + classes.tab}
          activeClassName={classes.Link_active}
        >
          Inventory
        </NavLink>

        <div className={classes.Runes}>
          {runes.map((runName, key) => (
            <img
              key={key}
              src={Assets.runes[runName]}
              className={classes.RuneImg}
            />
          ))}
        </div>
      </div>

      <div className={classes.StepButtonBox}>
        <div className={classes.StepButton} onClick={commitStep}>
          Step
        </div>
      </div>
    </div>
  );
};

export default MainMenu;
