export const query = {
  GET_NEW_ROUND_CARDS: "GET_NEW_ROUND_CARDS",
  COMMIT: "COMMIT",
  ADD_TABLE_CARDS_FOR_USER: "ADD_TABLE_CARDS_FOR_USER",
};
const appResources = {
  Cards: {
    getNewRoundCards: (params) => ({ type: query.GET_NEW_ROUND_CARDS }),
    sendNewTableCard: (card) => ({
      type: query.ADD_TABLE_CARDS_FOR_USER,
      card,
    }),
  },
  Round: {
    commit: () => ({ type: query.COMMIT }),
  },
};

export default appResources;
