import React from "react";
import classes from "./Cards.module.css";
import Card from "./Card";
import ScrollArea from "react-scrollbar";
import { useSelector } from "react-redux";
import {
  selectFilterCareer,
  selectShirtHandCard,
} from "Selectors/Selectors.js";

// ÐÐ°ÑÑÑ Ð½Ð° ÑÑÐ¾Ð»Ðµ
const Cards = ({ cards, space, hand, clickHandler }) => {
  const filteredCareer = useSelector(selectFilterCareer);
  if (filteredCareer && filteredCareer.length) {
    cards = cards.filter((el) =>
      filteredCareer.find((carFil) => el.career === carFil)
    );
  }
  const firstCard = useSelector(selectShirtHandCard);
  const fullWidth = {
    width: "100%",
  };
  return (
    <ScrollArea
      style={fullWidth}
      speed={0.8}
      contentClassName={classes.Content}
      horizontal={false}
      vertical={true}
    >
      <div>
        <React.Fragment>
          <div className={classes.Cards}>
            {cards.map((el, i) => {
              return (
                <Card
                  card={el}
                  space={space}
                  key={i}
                  hand={hand}
                  optional={{ firstCard }}
                  clickHandler={clickHandler ? clickHandler(el) : null}
                />
              );
            })}
          </div>
          <div style={{ height: "6vh" }}></div>
        </React.Fragment>
      </div>
    </ScrollArea>
  );
};

export default Cards;
