import React from "react";
import Assets from "Common/assets";

export default function DisplayItem({ card, space, width, height }) {
  const style = {
    width: width * 0.5 + "vh",
    height: height * 0.5 * (9 / 13) + "vh",
    backgroundImage: `url(${Assets[card.display]})`,
    margin: "0 auto",
    marginTop: "5vh",
    transition: "opacity 0.5s",
    backgroundRepeat: "no-repeat",
    opacity: card.shirt ? "0" : "1",
  };

  return <div style={style}></div>;
}
