import React from "react";
import Assets from "Common/assets";

export default function Panel({ card, space, width, height }) {
  const style = {
    background: "#CCA55E",
    width: width * 0.75 + "vh",
    height: height * 0.5 * (9 / 13) + "vh",
    marginLeft: width * 0.08 + "vh",
    borderRadius: "6px",
    marginTop: "1vh",
    border: "3px solid #3F2E1B",
    boxSizing: "border-box",
    opacity: card.shirt ? "0" : "1",
    transition: "opacity 0.5s",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  };

  const imageStyle = {
    height: width * 0.25 + "vh",
    paddingRight: "3px",
  };

  const getColor = (num) => {
    const colorHesh = { 1: "#AA1111", 2: "#DA5222", 3: "#468546" };
    return colorHesh[num];
  };

  const numberStyle = {
    fontWeight: "700",
    fontSize: "5vh",
    color: getColor(card.content.count),
  };

  const xStyle = {
    fontWeight: "600",
    color: "#3f2e1b",
    fontSize: "4vh",
  };
  return (
    <div style={style}>
      <img style={imageStyle} src={Assets[card.content.value]} />
      <span style={xStyle}>X&nbsp;</span>
      <span style={numberStyle}>{card.content.count}</span>
    </div>
  );
}
