import React from "react";
import Assets from "Common/assets";

const Rune = ({ rune, width, pos }) => {
  const fullStyle = {
    width: width * 0.9 + "vh",
    height: width * 0.9 + "vh",
    backgroundImage: "url(" + Assets.runes[rune] + ")",
    backgroundRepeat: "no-repeat",
    marginTop: width * 0.1 + "vh",
  };

  const firstStyle = {
    ...fullStyle,
    marginRight: width * 0.2 + "vh",
  };

  const style = pos ? fullStyle : firstStyle;
  return <div style={style}></div>;
};
export default Rune;
