import React from "react";
import Rune from "./Rune";

// ÐÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ ÑÑÐ½
const Runes = ({ runes, width, space }) => {
  // Ð ÑÐ½Ñ ÑÐ½Ð¸Ð·Ñ ÐºÐ°ÑÑÑ
  const cardBottom = {
    width: width * 0.85 + "vh",
    margin: "0 auto",
    marginTop: -space * 0.04 + "vh",
    height: space * 0.11 + "vh",
    display: "flex",
    justifyContent: "center",
  };

  return (
    <div style={cardBottom}>
      {runes
        .map((el, i) => {
          return <Rune rune={el} width={width / 4.5} pos={i} key={i} />;
        })
        .reverse()}
    </div>
  );
};

export default Runes;
