export default (state = {}, action) => {
  return {
    ...state,
    view: action.view,
  };
};

export const swapCardCreationReducer = (dialog) => {
  const { params } = dialog;
  let { firstCard, secondCard } = params;
  const tempShirt = firstCard.shirt;
  const tempShirtOther = firstCard.shirt_other;
  firstCard.shirt = !firstCard.shirt;
  firstCard.shirt_other = !firstCard.shirt_other;
  if (secondCard) {
    secondCard.shirt = tempShirt;
    secondCard.shirt_other = tempShirtOther;
  }

  return { ...dialog, params: { firstCard, secondCard } };
};
