import { CareerToRune } from "./Types";
import TableCardData from "./TableCard";

// ÐÐµÐ½ÐµÑÐ°ÑÐ¾Ñ Ð·Ð´Ð¾ÑÐ¾Ð²ÑÑ
function hpGenerator(card) {
  return {
    max: card.content.count,
    cur: card.content.count,
  };
}

// ÐÐµÐ½ÐµÑÐ°ÑÐ¾Ñ Ð¿ÑÐ¾Ð´ÑÐºÑÐ¾Ð², ÐºÐ¾ÑÐ¾ÑÑÐµ Ð¿ÑÐ¸Ð½Ð¾ÑÐ¸Ñ ÐºÐ°ÑÑÑ
function* productsGenerator(equipCard) {
  for (let i = 0; i < equipCard.content.count; ++i) {
    yield { type: equipCard.content.value };
  }
}

function* runesGenerator(equipCard) {
  const runeType = CareerToRune[equipCard.career];
  let count = equipCard.content.count - 1;
  if (count === 0) {
    count = 1;
  }
  for (let i = 0; i < count; ++i) {
    yield runeType;
  }
}

export default function generateCard(firstCard, secondCard) {
  let shirtCard = firstCard;
  let equipCard = secondCard;
  // Ð§ÑÐ¾Ð±Ñ ÑÐ¾ÑÐ½Ð¾ Ð·Ð½Ð°ÑÑ ÐºÐ°ÐºÐ°Ñ ÐºÐ°ÑÑÐ° ÑÑÐ±Ð°ÑÐºÐ¾Ð¹
  if (!firstCard.shirt) {
    shirtCard = secondCard;
    equipCard = firstCard;
  }

  const career = equipCard.career;
  const hunger = false;

  const hp = hpGenerator(shirtCard);
  const products = [...productsGenerator(equipCard)];
  const runes = [...runesGenerator(equipCard)];
  const bonuses = [];

  return new TableCardData(career, products, hp, runes, bonuses, hunger);
}
