import React, { useState } from "react";
import classes from "./CardCreationDialog.module.css";
import { useDispatch, useSelector } from "react-redux";
import Card from "../../Boards/Cards/Components/Cards/Card";
import {
  changeShirt,
  closeMainDialog,
  createTableCard,
  filter,
  swapCardCreationDialogParams,
  setMainCardTab,
  openSelectCardDialog,
  resetCards,
} from "ActionCreators/actionCreators";
import Assets from "Common/assets";
import generateCard from "GameLogic/Cards/TableCardGenerator";
import { MainTabs } from "Common/constants";
import { selectMainDialog } from "Selectors/Selectors";
import EmptyCard from "./EmptyCard";

const CardCreationDialog = ({ params }) => {
  const dialog = useSelector(selectMainDialog);
  const dispatch = useDispatch();
  const [animation, setAnimation] = useState(null);
  const [create, setCreate] = useState(false);
  const { firstCard, secondCard } = params;
  const canGenerate = firstCard && secondCard;

  const clickHandler = () => {
    dispatch(changeShirt(firstCard.id, secondCard?.id));
    dispatch(swapCardCreationDialogParams());
  };

  const closingAnimation = {
    transition: "transform 0.7s",
    transform: "translateY(110%)",
  };

  const cancelDialog = () => {
    setAnimation(closingAnimation);
    dispatch(resetCards());
    setTimeout(() => dispatch(closeMainDialog()), 500);
  };
  const sumStyle = {
    backgroundImage: `url(${canGenerate ? Assets.sum : Assets.sum_empty})`,
    backgroundRepeat: "no-repeat",
    width: "8vh",
    height: "8vh",
  };

  const resultStyle = {
    backgroundImage: `url(${
      canGenerate ? Assets.result : Assets.result_empty
    })`,
    backgroundRepeat: "no-repeat",
    width: "8vh",
    height: "8vh",
  };

  const showCard = () => {
    if (!canGenerate) {
      return null;
    }
    return generateCard(firstCard, secondCard);
  };

  const generatedCard = showCard();

  const createAnimatom = {
    transition: "opacity 0.7s",
    opacity: "0",
  };

  const createCard = () => {
    if (!generatedCard) {
      return;
    }
    setAnimation(createAnimatom);
    setTimeout(() => {
      // ÐÐ°Ð¿ÑÑÐº Ð°Ð½Ð¸Ð¼Ð°ÑÐ¸Ð¸ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ñ ÐºÐ°ÑÑÑ
      setCreate(true);
      setAnimation(null);
    }, 0);
    setTimeout(() => {
      // Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ°ÑÑÑ Ð² Ð¿Ð°Ð¼ÑÑÐ¸
      dispatch(createTableCard(generatedCard, [firstCard.id, secondCard.id]));
      // ÐÐ°ÐºÑÑÑÐ¸Ðµ Ð´Ð¸Ð°Ð»Ð¾Ð³Ð°
      dispatch(closeMainDialog());
      // ÐÑÐºÑÑÑÐ¸Ðµ Ð²ÐºÐ»Ð°Ð´ÐºÐ¸ Ñ ÐºÐ°ÑÑÐ°Ð¼Ð¸ Ð½Ð° ÑÑÐºÐµ
      dispatch(setMainCardTab(MainTabs.TABLE));
      // ÐÐ¾Ð¾Ð±ÑÐµ Ð½Ðµ Ð¿Ð¾Ð¼Ð½Ñ ÑÑÐ¾ ÑÑÑ Ð¿ÑÐ¾Ð¸ÑÑÐ¾Ð´Ð¸Ñ
      dispatch(filter(null));
    }, 1000);
  };

  const changeCardHandlerFactory = (replacedId) => () => {
    // dispatch(filter(null));
    dispatch(
      openSelectCardDialog(dialog, [firstCard.id, secondCard.id], replacedId)
    );
  };

  const addCard = () => {
    dispatch(openSelectCardDialog(dialog, [firstCard.id], null));
  };

  if (create) {
    return (
      <div className={classes.TargetCardCreated}>
        <div>
          <Card card={generatedCard} space={79} />
        </div>
      </div>
    );
  }

  return (
    <div className={classes.CardCreationDialogWrapper} style={animation}>
      <div className={classes.Header}>Card creation</div>
      <div className={classes.Main}>
        <div className={classes.CardContainer}>
          <Card
            card={firstCard}
            space={70}
            hand={true}
            clickHandler={clickHandler}
          />
          <div
            className={classes.ChangeButton}
            onClick={changeCardHandlerFactory(firstCard.id)}
          >
            Change
          </div>
        </div>
        <div className={classes.SumWrapper}>
          <div style={sumStyle}></div>
          <div className={classes.SumBottom}>&nbsp;</div>
        </div>
        <div className={classes.CardContainer}>
          {!secondCard ? (
            <React.Fragment>
              <EmptyCard
                space={70}
                asset={Assets.card_addition}
                onClick={addCard}
              />
              <div className={classes.ChangeButton} onClick={addCard}>
                Add card
              </div>
            </React.Fragment>
          ) : (
            <React.Fragment>
              <Card
                card={secondCard}
                space={70}
                hand={true}
                clickHandler={clickHandler}
              />
              <div
                className={classes.ChangeButton}
                onClick={changeCardHandlerFactory(secondCard.id)}
              >
                Change
              </div>
            </React.Fragment>
          )}
        </div>
        <div className={classes.TargetCardWrapper}>
          <div className={classes.SumWrapper}>
            <div style={resultStyle}></div>
            <div className={classes.SumBottom}>&nbsp;</div>
          </div>
          <div>
            {generatedCard ? (
              <Card card={generatedCard} space={79} />
            ) : (
              <EmptyCard space={79} asset={Assets.empty_new_card} />
            )}
          </div>
        </div>
      </div>
      <div className={classes.ButtonsWrapper}>
        <div className={classes.Cancel} onClick={cancelDialog}>
          Cancel
        </div>
        <div className={classes.Gutter}>&nbsp;</div>
        <div
          className={generatedCard ? classes.Create : classes.Create__disabled}
          onClick={createCard}
        >
          Create
        </div>
      </div>
    </div>
  );
};

export default CardCreationDialog;
