export const rollCardReducer = (state, card_id) => {
  return state.map((card) => {
    if (card.id === card_id) {
      return { ...card, shirt: !card.shirt, shirt_other: false };
    } else {
      return { ...card, shirt_other: !card.shirt_other };
    }
  });
};

export const setFilter = (state, action) => {
  let newCareer;
  if (state.career) {
    // Ð£Ð¶Ðµ Ð½Ðµ Ð¿ÑÑÑÐ¾
    if (action.career) {
      // ÐÐ°Ð´Ð°Ð¼ Ð½Ð¾Ð²ÑÐ¹ Ð¸Ð»Ð¸ ÑÐ´Ð°Ð»ÑÐµÐ¼
      if (state.career.find((el) => el === action.career)) {
        newCareer = state.career.filter((el) => el !== action.career);
      } else {
        newCareer = [...state.career, action.career];
      }
    }
  } else {
    if (!action.career) {
      newCareer = null;
    } else {
      newCareer = [action.career];
    }
  }

  const newHandCards = state.handCards.map((card) => ({
    ...card,
    shirt: false,
    shirt_other: false,
  }));

  return {
    ...state,
    career: newCareer,
    handCards: !action.career ? newHandCards : state.handCards,
  };
};

// Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ°ÑÑÑ Ð´Ð»Ñ ÑÑÐ¾Ð»Ð°
export const createCardReduced = (state, action) => {
  const { newCard, deleteIds } = action;
  return {
    ...state,
    tableCards: [newCard, ...state.tableCards],
    handCards: state.handCards.filter(
      (card) => !deleteIds.find((idToDelete) => idToDelete === card.id)
    ),
  };
};

// Ð¢Ð¾ÑÐµÑÐ½Ð¾Ðµ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ðµ Ð¿ÐµÑÐµÐ²Ð¾ÑÐ¾ÑÐ° ÐºÐ°ÑÑÑ
export const setShirtReducer = (state, action) => {
  const { idFirst, idSecond } = action;
  return state.map((card) => {
    if (card.id === idFirst || card.id === idSecond) {
      return { ...card, shirt: !card.shirt, shirt_other: !card.shirt_other };
    }
    return card;
  });
};

