import React, { useState } from "react";
import classes from "./NewCardsPresentation.module.css";
import { useDispatch } from "react-redux";
import { closeMainDialog } from "ActionCreators/actionCreators";
import GameCards from "Views/GameBoard/Boards/Cards/Components/Cards/Cards.js";

export default function NewCardsPresentation({ params }) {
  const { newHandCards } = params;
  const dispatch = useDispatch();
  const [animation, setAnimation] = useState(null);

  const closingAnimation = {
    transition: "transform 0.3s",
    transform: "scale(0)",
  };

  const cancelDialog = () => {
    setAnimation(closingAnimation);
    setTimeout(() => {
      dispatch(closeMainDialog());
    }, 300);
  };

  const newHandCardsClone = newHandCards.map((card) => ({
    ...card,
    select: true,
  }));

  const space = (45 * 5) / newHandCardsClone.length;
  return (
    <div className={classes.SelectDialog} style={animation}>
      <div className={classes.Header}>You got new cards!</div>
      <div className={classes.GameCards}>
        <div className={classes.Wrapper}>
          <GameCards
            cards={newHandCardsClone}
            space={space}
            hand={true}
            select={true}
            clickHandler={() => () => {}}
          />
        </div>
      </div>
      <div className={classes.ButtonsWrapper}>
        <div className={classes.Cancel} onClick={cancelDialog}>
          OK
        </div>
      </div>
    </div>
  );
}
