import { takeEvery, put, call } from "redux-saga/effects";
import C from "Common/constants";
import { getNickname } from "../AirConsole/client";
import {
  changeView,
  setNickname,
  setMyTurn,
  resetCards,
  addHandCards,
  openNewHandCardsDialog,
} from "ActionCreators/actionCreators";
import { VIEW_NAMES } from "Common/constants";
import { getDeviceId, request } from "../AirConsole/client";
import appResources from "Providers/appResources";
import generateHandCards from "GameLogic/Cards/HandCardGenerator";

export function* sagaWatcher() {
  yield takeEvery(C.READY, sagaReady);
  yield takeEvery(C.SET_PLAYER_TURN, sagaSetPlayerTurn);
  yield takeEvery(C.NEXT_PLAYER, sagaNewPlayer);
  yield takeEvery(C.GIVE_NEW_ROUND_CARDS, sagaGetNewRoundCards);
  yield takeEvery(C.CREATE_TABLE_CARD, sagaSenCreatedCard);
}

function* sagaReady() {
  // ÐÐ¿ÑÐµÐ´ÐµÐ»Ð¸ÑÑ Ð½Ð¸ÐºÐ½ÐµÐ¹Ð¼
  yield put(setNickname(getNickname()));
  // Ð¡Ð¼ÐµÐ½Ð¸ÑÑ Ð¿ÑÐµÐ´ÑÑÐ°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð½Ð° Ð²ÑÐ±Ð¾Ñ Ð¿ÐµÑÑÐ¾Ð½Ð°Ð¶ÐµÐ¹
  yield put(changeView(VIEW_NAMES.AVATAR_SELECTION));
}

function* sagaSetPlayerTurn({ playerTurn }) {
  // ÐÐ¿ÑÐµÐ´ÐµÐ»ÑÐµÐ¼ ÑÐµÐ¹ ÑÐ¾Ð´
  const isMyTurn = getDeviceId() === playerTurn;
  console.log("my turn");
  console.log(isMyTurn);

  yield put(setMyTurn(isMyTurn));
}

function* sagaNewPlayer(action) {
  yield call(() => request(appResources.Round.commit()));
}

