import React from "react";
import Assets from "Common/assets";

export default function EmptyCard({ space, asset, onClick }) {
  const width = space * 0.449;
  const height = space * 0.65;
  // Ð¡ÑÐ¸Ð»Ñ ÑÐ¾Ð´Ð° Ð´ÐµÑÑÐµÐ»ÑÐ½Ð¾ÑÑÐ¸
  const careerStyle = {
    marginLeft: "10px",
    height: height + "vh",
    width: width + "vh",
    backgroundImage: `url(${asset})`,
    backgroundRepeat: "no-repeat",
    marginTop: "3vh",
    marginBottom: "3vh",
    boxSizing: "border-box",
    position: "relative",
  };

  return <div style={careerStyle} onClick={onClick}></div>;
}
