import React, { useEffect } from "react";

export default function ModalComponent({ active, children, setActive }) {
  useEffect(() => {
    if (!active) {
      return;
    }
    const clickOutOfMenu = (event) => {
      event.preventDefault();
      setActive(false);
    };
    document.addEventListener("click", clickOutOfMenu);
    document.addEventListener("contextmenu", clickOutOfMenu);

    return () => {
      document.removeEventListener("click", clickOutOfMenu);
      document.removeEventListener("contextmenu", clickOutOfMenu);
    };
  }, [active]);

  if (!active) {
    return null;
  }

  return <React.Fragment>{children}</React.Fragment>;
}
