import React from "react";
import classes from "./Avatar.module.css";

// ÐÐ°Ð´Ð¾ Ð²ÑÐ½ÐµÑÑÐ¸ Ð² json Ð¸Ð»Ð¸ Ð² assets
const colors = ["#55A055", "#880000", "#746482", "#79A1AA"];
const imagesPath = "../../images_c/avatars/";
const avatarColors = [
  "greenAvatar.svg",
  "redAvatar.svg",
  "violetAvatar.svg",
  "blueAvatar.svg",
];
const avatarImages = [
  "avatarMonarh.svg",
  "avatarViking.svg",
  "avatarPrincess.svg",
  "avatarKnight.svg",
];

// ÐÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ Ð°Ð²Ð°ÑÐ°ÑÐ°
export default ({ imageID, nickName, colorID, isReady, changeState }) => {
  let avatar = {
    width: "200px",
    height: "200px",
    backgroundImage: "url(" + imagesPath + avatarColors[colorID] + ")",
    position: "relative",
    overflow: "hidden",
  };

  const readuBox = (
    <span
      className={classes.readyStyle}
      onClick={changeState}
      style={{
        backgroundColor: isReady ? "#55A055" : "#880000",
      }}
    >
      {isReady ? "ready" : "prepare"}
    </span>
  );
  const flexText = { fontSize: 35 };
  if (nickName.length > 14) {
    nickName = nickName.slice(0, 14);
  }

  while (nickName.length * (flexText.fontSize * 0.6) > 200) {
    flexText.fontSize--;
  }

  flexText.fontSize += "px";
  return (
    <div>
      <div style={avatar}>
        <img
          className={classes.imgStyle}
          src={imagesPath + avatarImages[imageID]}
        />
      </div>
      <span className={classes.nickStyle} style={flexText}>
        {nickName}
      </span>
      {readuBox}
    </div>
  );
};
