import React from "react";
import classes from "./Table.module.css";
import GameCards from "../Components/Cards";
import Filter from "../Components/Filter";
import { useSelector } from "react-redux";
import { selectTableCards } from "Selectors/Selectors";
import Assets from "Common/assets";

const Table = (props) => {
  const tableCards = useSelector(selectTableCards);
  const emptyTable = tableCards.length === 0;
  const emptyTableStyles = {
    width: "60%",
    height: "50%",
    backgroundRepeat: "no-repeat",
    backgroundImage: `url(${Assets.empty_table})`,
  };

  return (
    <div className={classes.Table}>
      <Filter />
      {!emptyTable ? <GameCards space={52} cards={tableCards} /> : null}
      {!emptyTable ? null : (
        <div className={classes.EmptyWrapper}>
          <div style={emptyTableStyles}></div>
        </div>
      )}
    </div>
  );
};

export default Table;
