import React from "react";
import classes from "./Cards.module.css";
import Table from "./Table";
import Hand from "./Hand";
import { useDispatch, useSelector } from "react-redux";
import { selectMainCardTab } from "Selectors/Selectors";
import {
  setMainCardTab,
  openCareerSelectionDialog,
} from "ActionCreators/actionCreators";

const MainTabs = {
  HAND: "HAND",
  TABLE: "TABLE",
};

const Content = {
  HAND: <Hand />,
  TABLE: <Table />,
};

const Cards = (props) => {
  const dispatch = useDispatch();
  const mainTab = useSelector(selectMainCardTab);
  const setMainTab = (value) => {
    dispatch(setMainCardTab(value));
  };
  // const [mainTab, setMainTab] = useState(MainTabs.HAND);
  const isActive = (tabName) => {
    if (tabName === mainTab) {
      return " " + classes.MainTabItem_active;
    }
    return "";
  };

  const onCreateClick = () => {
    dispatch(openCareerSelectionDialog());
  };

  return (
    <div className={classes.Cards}>
      <div className={classes.MainTabs}>
        <div
          className={classes.MainTabItem + isActive(MainTabs.HAND)}
          onClick={() => setMainTab(MainTabs.HAND)}
        >
          Hand
        </div>
        <div
          className={classes.MainTabItem + isActive(MainTabs.TABLE)}
          onClick={() => setMainTab(MainTabs.TABLE)}
        >
          Table
        </div>
      </div>
      <div className={classes.Content}>{Content[mainTab]}</div>
      <div className={classes.CardCreationButtonWrapper}>
        <div className={classes.CardCreationButton} onClick={onCreateClick}>
          Create card
        </div>
      </div>
    </div>
  );
};

export default Cards;
