import React from "react";
import Assets from "Common/assets";

// ÐÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ ÑÐ»ÐµÐ¼ÐµÐ½ÑÐ¾Ð² Ð¿ÑÐ¾Ð¸Ð·Ð²Ð¾Ð´ÑÑÐ²Ð° ÐºÐ°ÑÑÑ
const Products = ({ products, width }) => {
  return products.map((el, i) => {
    const style = {
      marginTop: width * 0.3 + "vh",
      marginLeft: i ? "3px" : "10px",
      width: width * 0.9 + "vh",
      height: width * 0.9 + "vh",
      backgroundImage: "url(" + Assets[el.type] + ")",
      backgroundRepeat: "no-repeat",
    };
    return <div key={i} style={style}></div>;
  });
};

const ProductContainer = ({ products, space, width }) => {
  // ÐÐµÑÑÑÑÐºÐ° ÐºÐ°ÑÑÑ
  const cardHead = {
    height: space * 0.11 + "vh",
    display: "flex",
  };
  return (
    <div style={cardHead}>
      <Products products={products} width={width / 5} />
    </div>
  );
};

export default ProductContainer;
