import React from "react";
import { useDispatch } from "react-redux";
import { rollCard, openCreateDialog } from "ActionCreators/actionCreators";
import Assets from "Common/assets";
export default function Roller({
  card,
  space,
  width,
  height,
  clickHandler,
  optional,
}) {
  const dispatch = useDispatch();
  const style = {
    height: width * 0.25 + "vh",
    position: "absolute",
    bottom: -width * 0.25 * 0.5 + "vh",
    transition: "transform 0.5s",
    right: width * 0.25 * 1.65 + "vh",
    transform: card.shirt ? "rotateZ(-180deg)" : "none",
  };

  const openCardCreationDialog = () => {
    dispatch(openCreateDialog(optional?.firstCard, card));
  };
  // const image = shirt ? Assets.stick_card : Assets.roll_card;
  const defaultClickHandler = card.shirt_other
    ? openCardCreationDialog
    : () => {
        dispatch(rollCard(card.id));
      };

  let image = Assets.roll_card;
  if (card.shirt_other && !card.creation) {
    image = Assets.stick_card;
    style.transform = "rotateZ(180deg)";
  }

  if (card.select) {
    image = null;
    style.transform = "none";
  }

  clickHandler = clickHandler ? clickHandler : defaultClickHandler;
  return image ? (
    <img style={style} src={image} onClick={clickHandler} alt={"Roller"} />
  ) : null;
}
