import React from "react";
import { useDispatch, useSelector } from "react-redux";
import classes from "./DialogWrapper.module.css";
import CardCreationDialog from "./CardCreation";
import { DIALOG_NAMES } from "Common/constants";
import { selectMainDialog } from "Selectors/Selectors";
import {
  closeMainDialog,
  resetCards,
  filter,
} from "ActionCreators/actionCreators";
import SelectDialog from "./SelectDialog";
import SelectCareerDialog from "./SelectCareerDialog";
import NewCardsPresentation from "./NewCardsPresentation";

// ÐÑÐµ Ð¿ÑÐµÐ´ÑÑÐ°Ð²Ð»ÐµÐ½Ð¸Ñ
const dialogs = {
  [`${DIALOG_NAMES.CardCreation}`]: CardCreationDialog,
  [`${DIALOG_NAMES.SelectDialog}`]: SelectDialog,
  [`${DIALOG_NAMES.SelectCareerDialog}`]: SelectCareerDialog,
  [`${DIALOG_NAMES.NewCardsPresentation}`]: NewCardsPresentation,
};

// Ð Ð·Ð°Ð²Ð¸ÑÐ¸Ð¼Ð¾ÑÑÐ¸ Ð¾Ñ Ð¿ÐµÑÐµÐ´Ð°Ð½Ð½Ð¾Ð³Ð¾ ÐµÐ¼Ñ view - Ð²Ð¾Ð·Ð²ÑÐ°ÑÐ°ÐµÑ ÑÐµÐ°ÐºÑ Ð¿ÑÐµÐ´ÑÑÐ²Ð°Ð»ÐµÐ½Ð¸Ðµ
const DialogWrapper = () => {
  const dispatch = useDispatch();
  const dialog = useSelector(selectMainDialog);
  if (!dialog) {
    return null;
  }

  const { dialogName, params } = dialog;
  const Dialog = dialogs[dialogName];
  const cancelDialog = () => {
    dispatch(filter(null));
    dispatch(resetCards());
    dispatch(closeMainDialog());
  };
  return (
    <div className={classes.DialogConteiner}>
      <div className={classes.DialogWrapper} onClick={cancelDialog}></div>
      <Dialog params={params} />
    </div>
  );
};

export default DialogWrapper;
