import React from "react";
import Assets from "Common/assets";

// ÐÑÐ¾Ð±ÑÐ°Ð¶ÐµÐ½Ð¸Ðµ ÑÐµÑÐ´ÐµÑÐµÐº
const Hearts = ({ HP, width }) => {
  // Ð¡ÑÐ¸Ð»Ñ ÑÐµÑÐ´ÐµÑÐµÐº
  const fullStyle = {
    width: width * 0.9 + "vh",
    height: width * 0.9 + "vh",
    marginTop: "0",
    marginLeft: "5px",
    backgroundImage: "url(" + Assets.HP + ")",
    backgroundRepeat: "no-repeat",
  };

  // Ð¡ÑÐ¸Ð»Ñ Ð¿ÐµÑÐ²Ð¾Ð³Ð¾ ÑÐµÑÐ´ÐµÑÐºÐ° - Ð½ÑÐ¶ÐµÐ½ Ð¾ÑÑÑÑÐ¿
  const firstHeart = {
    ...fullStyle,
    marginTop: width * 0.5 + "vh",
  };

  // Ð¡ÑÐ¸Ð»Ñ Ð½ÐµÐ´Ð¾ÑÑÐ°ÑÑÐµÐ³Ð¾ ÑÐµÑÐ´ÐµÑÐºÐ°
  const noHPfullStyle = {
    ...fullStyle,
    backgroundImage: "url(" + Assets.NOHP + ")",
  };

  const heartsArr = [];
  for (let i = 0; i < HP.cur; ++i) {
    heartsArr.push(
      <div style={i ? fullStyle : firstHeart} key={i}>
        {" "}
        &nbsp;
      </div>
    );
  }

  const lostHP = HP.max - HP.cur;
  for (let i = 0; i < lostHP; ++i) {
    heartsArr.push(
      <div style={noHPfullStyle} key={i + "lost"}>
        {" "}
        &nbsp;
      </div>
    );
  }

  return <React.Fragment>{heartsArr}</React.Fragment>;
};

export default Hearts;
