import React from "react";
import classes from "./Item.module.css";
import Assets from "Common/assets";
import tables from "GameLogic";
import { Draggable, Droppable } from "react-beautiful-dnd";

export const itemTypeToAsset = (type) => {
  return Assets[type];
};

export const itemTypeToColor = (type) => {
  const isNotDefault = Object.keys(tables.Ð¡areer.items).find((career) => {
    return tables.Ð¡areer.items[career].includes(type);
  });

  if (isNotDefault) {
    return tables.Ð¡areer.colors[isNotDefault];
  }

  return tables.Ð¡areer.colors.default;
};

export default (item) => {
  const { type, count, action, index } = item;
  const imgStyle = {
    opacity: 1,
    width: "15vh",
    height: "15vh",
    zIndex: 100,
  };

  const itemColor = {
    position: "absolute",
    width: "20vh",
    height: "20vh",
    opacity: 0.5,
    background: `linear-gradient(to top left, ${itemTypeToColor(
      type
    )}, #da824f)`,
  };

  return (
    <div className={classes.Wrapper}>
      <Droppable droppableId={type + "cell" || `cell-${index}`}>
        {(provided) => (
          <div
            ref={provided.innerRef}
            {...provided.droppableProps}
            className={classes.RoundWrapper}
          >
            <Draggable draggableId={type || `item-${index}`} index={index}>
              {(provided) => (
                <div
                  onContextMenu={(e) => {
                    e.preventDefault();
                    e.stopPropagation();
                  }}
                  ref={provided.innerRef}
                  {...provided.draggableProps}
                  {...provided.dragHandleProps}
                  className={classes.RoundWrapper1}
                >
                  {!type ? null : (
                    <React.Fragment>
                      <div className={classes.Round} style={itemColor}></div>
                      <img
                        onContextMenu={(e) => e.preventDefault()}
                        src={itemTypeToAsset(type)}
                        style={imgStyle}
                        alt={type}
                      />
                    </React.Fragment>
                  )}
                  <div className={classes.Count}>{count}</div>
                </div>
              )}
            </Draggable>
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </div>
  );
};
