export const openCreateDialog = (firstCard, secondCard) => {
  return {
    type: C.OPEN_CREATION_DIALOG,
    dialog: {
      dialogName: DIALOG_NAMES.CardCreation,
      params: {
        firstCard: firstCard ? { ...firstCard, creation: true } : null,
        secondCard: secondCard ? { ...secondCard, creation: true } : null,
      },
    },
  };
};

