import React, { useState } from "react";
import classes from "./Card.module.css";

import TableContent from "./TableContent";
import Assets, { Career, imagesPath } from "Common/assets";
import Roller from "./Roller";
import DisplayItem from "./DisplayItem";
import Panel from "./Panel";
import ModalComponent from "Containers/ModalComponent";
import CardContextMenu from "./CardContextMenu";
import CardOperations from "GameLogic/CardOperations";

// Ð­ÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ ÐºÐ°ÑÑÑ
const Card = ({ card, space, hand, clickHandler, optional }) => {
  const [activeContextMenu, setContextMenuActive] = useState(false);
  const cardOperations = new CardOperations(card).getOperations();
  const width = space * 0.449;
  const height = space * 0.65;

  // Ð¡ÑÐ¸Ð»Ñ ÑÐ¾Ð´Ð° Ð´ÐµÑÑÐµÐ»ÑÐ½Ð¾ÑÑÐ¸
  const careerStyle = {
    marginLeft: "10px",
    height: height + "vh",
    width: width + "vh",
    backgroundImage:
      "url(" + (card.shirt ? Career.card : Career[card.career]) + ")",
    backgroundRepeat: "no-repeat",
    marginTop: "3vh",
    marginBottom: hand ? "3vh" : "6vh",
    boxSizing: "border-box",
    transition: !card.HP ? "transform 0.5s, background 0.25s 0.1s" : "none",
    transform: card.shirt ? "rotateY(180deg)" : "none",
    transformOrigin: "46% 0",
    position: "relative",
  };

  const cls = [];
  if (card.hunger) {
    cls.push("hunger");
  }

  const addClasses = cls.map((cl) => classes[cl]).join(" ");

  // ÐÐ»Ð¸Ðº Ð¿Ð¾ ÑÐ°Ð¼Ð¾Ð¹ ÐºÐ°ÑÑÐµ (Ð½Ð°Ð¿ÑÐ¸Ð¼ÐµÑ Ð²ÑÐ±Ð¾Ñ ÐºÐ°ÑÑÑ)
  const onClickHandler = (event) => {
    if (!clickHandler) {
      return;
    }
    if (card.select) {
      clickHandler(event);
    }
  };

  // ÐÐºÑÐ¸Ð²Ð°ÑÐ¸Ñ ÐºÐ¾Ð½ÑÐµÐºÑÑÐ½Ð¾Ð³Ð¾ Ð¼ÐµÐ½Ñ + onLong tap
  const onContextMenuHandler = (event) => {
    event.preventDefault();
    if (card.shirt) {
      return;
    }
    setContextMenuActive(true);
  };

  return (
    <div
      style={careerStyle}
      className={addClasses}
      onClick={onClickHandler}
      onContextMenu={onContextMenuHandler}
    >
      {!hand ? (
        <TableContent
          space={space}
          products={card.products}
          width={width}
          card={card}
          height={height}
        />
      ) : (
        <React.Fragment>
          <DisplayItem
            space={space}
            width={width}
            card={card}
            height={height}
          />
          <Panel space={space} width={width} card={card} height={height} />
          <Roller
            space={space}
            width={width}
            card={card}
            height={height}
            clickHandler={clickHandler}
            optional={optional}
          />
        </React.Fragment>
      )}
      <ModalComponent
        active={activeContextMenu && !card.shirt}
        setActive={setContextMenuActive}
      >
        <CardContextMenu
          options={cardOperations}
          width={width}
          height={height}
        />
      </ModalComponent>
    </div>
  );
};

export default Card;
