import React, { useState } from "react";
import classes from "./Filter.module.css";
import Assets, { Career } from "Common/assets";
import { useSelector, useDispatch } from "react-redux";
import { selectFilterCareer } from "Selectors/Selectors.js";
import { filter } from "ActionCreators/actionCreators";
import { Career as CareerNames } from "GameLogic/Cards/Types";

const FilterItem = ({ career }) => {
  const dispatch = useDispatch();
  const activeFilter = useSelector(selectFilterCareer);
  // Ð¡ÑÐ¸Ð»Ñ ÑÐ¾Ð´Ð° Ð´ÐµÑÑÐµÐ»ÑÐ½Ð¾ÑÑÐ¸
  const careerStyle = (career, active) => ({
    // marginLeft: "10px",
    height: "17vh",
    width: "10vh",
    backgroundImage: "url(" + career + ")",
    backgroundRepeat: "no-repeat",
    // marginTop: "3vh",
    // marginBottom: "auto",
    transform: active ? "rotate(-90deg)" : "none",
    transition: "transform 0.3s",
    boxSizing: "border-box",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",
    alignItems: "center",
  });

  const itemStyle = (item) => ({
    height: "8vh",
    width: "6vh",
    backgroundRepeat: "no-repeat",
    backgroundImage: "url(" + item + ")",
  });

  const itemHash = {
    [Career.smith_bold]: Assets.smith_smith,
    [Career.farmer_bold]: Assets.apple,
    [Career.warrior_bold]: Assets.damage,
    [Career.miner_bold]: Assets.pickaxe,
  };

  const careerHash = {
    [Career.smith_bold]: CareerNames.smith,
    [Career.farmer_bold]: CareerNames.farmer,
    [Career.warrior_bold]: CareerNames.warrior,
    [Career.miner_bold]: CareerNames.miner,
  };

  const clickHangler = () => {
    if (activeFilter !== careerHash[career]) {
      dispatch(filter(careerHash[career]));
    } else {
      dispatch(filter(null));
    }
  };

  return (
    <div
      style={careerStyle(
        career,
        activeFilter && activeFilter.find((el) => el === careerHash[career])
      )}
      onClick={clickHangler}
    >
      <div style={itemStyle(itemHash[career])}></div>
    </div>
  );
};

const Filter = (props) => {
  const dispatch = useDispatch();
  const [animatedBack, changeBack] = useState(
    "url(" + Career.all_cards_bold + ")"
  );
  const allCardsStyle = {
    height: "14vh",
    width: "9vw",
    marginTop: "1vh",
    backgroundRepeat: "no-repeat",
    backgroundImage: animatedBack,
    transition: "0.3s",
  };

  const resetAnimation = () => {
    setTimeout(() => changeBack("url(" + Career.all_cards_bold_1 + ")"), 100);
    setTimeout(() => changeBack("url(" + Career.all_cards_bold_2 + ")"), 200);
    setTimeout(() => changeBack("url(" + Career.all_cards_bold + ")"), 300);
  };

  const resetFilter = () => {
    // dispatch({ type: "GIVE_NEW_ROUND_CARDS", countNewCards: 3 });
    dispatch(filter(null));
    resetAnimation();
  };
  return (
    <div className={classes.Filter}>
      <div style={allCardsStyle} onClick={resetFilter}></div>
      <FilterItem career={Career.smith_bold} />
      <FilterItem career={Career.farmer_bold} />
      <FilterItem career={Career.warrior_bold} />
      <FilterItem career={Career.miner_bold} />
    </div>
  );
};

export default Filter;
